// main.js
import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import emailjs from 'emailjs-com'

// ‚Äî‚Äî CSRF Token ÈÖçÁΩÆ ‚Äî‚Äî  
const tokenMeta = document.querySelector('meta[name="csrf-token"]')
if (tokenMeta) {
  const csrfToken = tokenMeta.getAttribute('content')
  axios.defaults.headers.common['X-CSRFToken'] = csrfToken
  axios.defaults.xsrfCookieName = 'csrftoken'
  axios.defaults.xsrfHeaderName = 'X-CSRFToken'
}

// ‚Äî‚Äî ÂàùÂßãÂåñ EmailJS ‚Äî‚Äî  
// ËØ∑Á°ÆËÆ§ËøôÈáåÁöÑ Public Key ‰∏éÊéßÂà∂Âè∞ÈáåÁúãÂà∞ÁöÑ‰∏ÄËá¥
emailjs.init('Zt0XmmEON9ohWldYP')

const app = createApp(App)

app.use(store)
app.use(router)

// ÂèØÈÄâÔºöÊää axios ÊåÇÂà∞ÂÖ®Â±ÄÔºåÁªÑ‰ª∂ÈáåÁõ¥Êé• this.$axios
app.config.globalProperties.$axios = axios

app.mount('#app')
